import { getAnimeDetail } from '@/lib/api'
import { Navbar } from '@/components/navbar'
import Image from 'next/image'
import { Button } from '@/components/ui/button'
import {
  Play,
  Heart,
  Share2,
  Star,
  Calendar,
  Film,
  BookOpen, // Ikon untuk Source
  Activity // Ikon untuk Status
} from 'lucide-react'
import { notFound } from 'next/navigation'

// --- DEFINISI TIPE UNTUK MENGHILANGKAN ERROR TS2339 ---
// Ini adalah struktur minimum yang dibutuhkan oleh komponen ini
interface Genre {
  mal_id: number;
  name: string;
}

interface RelationEntry {
  mal_id: number;
  name: string;
}

interface Relation {
  relation: string;
  entry: RelationEntry[];
}

interface Anime {
  title: string;
  title_english: string | null;
  score: number | null;
  episodes: number | null;
  status: string | null;
  year: number | null;
  synopsis: string | null;
  source: string | null;

  images: {
    jpg: {
      large_image_url: string;
    }
  } | null;

  aired: {
    from: string | null;
    to: string | null;
  } | null;

  trailer: {
    url: string | null;
  } | null;

  genres: Genre[];
  relations: Relation[];
}
// --- END DEFINISI TIPE ---

interface AnimePageProps {
  params: Promise<{ id: string }>
}

// Komponen Pembantu untuk menampilkan list relasi
const RelatedContent = ({ relations }: { relations: Relation[] }) => {
  if (!relations || relations.length === 0) return null;

  return (
    <section className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8 mb-12">
      <h2 className="text-2xl font-bold text-foreground mb-4">Related Content</h2>
      <div className="space-y-4">
        {relations.map((rel, index) => (
          <div key={index} className="flex gap-4 p-3 bg-card rounded-lg border border-border items-center">
            {/* Mengganti rel.relation menjadi string yang diformat */}
            <span className="font-semibold text-primary min-w-[120px]">{rel.relation}:</span>
            <div className="flex flex-wrap gap-2">
              {rel.entry.map((entry) => (
                <a
                  key={entry.mal_id}
                  href={`/anime/${entry.mal_id}`}
                  className="text-muted-foreground hover:text-foreground hover:underline transition-colors"
                  target="_blank" // Buka di tab baru
                  rel="noopener noreferrer"
                >
                  {entry.name}
                </a>
              ))}
            </div>
          </div>
        ))}
      </div>
    </section>
  );
};


export default async function AnimePage({ params }: AnimePageProps) {
  const { id } = await params
  const animeId = parseInt(id)
  if (isNaN(animeId)) notFound()

  let anime: Anime | null = null // Terapkan tipe Anime yang baru
  try {
    // getAnimeDetail harus mengembalikan tipe Anime yang benar
    anime = await getAnimeDetail(animeId) as Anime
  } catch (err) {
    console.error('Error fetching anime:', err)
    notFound()
  }

  if (!anime) notFound()

  // --- MENGAMBIL DATA BARU ---
  const imageUrl = anime.images?.jpg?.large_image_url || '/placeholder.svg'
  const titleEnglish = anime.title_english || '-'
  const score = anime.score != null ? anime.score.toFixed(1) : '-'
  const episodes = anime.episodes ?? 'TBA'
  const source = anime.source || '-' // Sumber (Manga, Original, dll.)
  const status = anime.status || '-' // Status Airing
  const year = anime.year ?? 'TBA'
  const trailerUrl = anime.trailer?.url // URL Trailer
  const genres = anime.genres ?? []
  const synopsis = anime.synopsis || '-'
  const airedFrom = anime.aired?.from ? new Date(anime.aired.from).toLocaleDateString() : 'TBA'
  const airedTo = anime.aired?.to ? new Date(anime.aired.to).toLocaleDateString() : 'Ongoing'
  const relations = anime.relations ?? []

  return (
    <main className="min-h-screen bg-background overflow-x-hidden">

      <div className="fixed top-0 left-0 w-full z-50">
        <Navbar />
      </div>

      {/* HERO BACKGROUND */}
      <div className="relative h-112 md:h-128 w-full overflow-hidden">
        <Image
          src={imageUrl}
          alt="Anime background"
          fill
          className="object-cover blur-sm"
          quality={80}
        />

        {/* Overlay gradient di bagian bawah agar teks di atas poster terbaca */}
        <div className="absolute inset-0 bg-gradient-to-t
          from-background via-background/20 to-transparent">
        </div>
      </div>

      {/* CONTENT WRAPPER */}
      <div className="relative z-20 max-w-6xl mx-auto px-4 sm:px-6 lg:px-8 -mt-24 md:-mt-40 grid grid-cols-1 md:grid-cols-3 gap-8">

        {/* POSTER (MD: COL 1) */}
        <div className="relative w-44 md:w-60 mx-auto md:mx-0">
          <div className="shadow-2xl rounded-lg overflow-hidden group transition-transform duration-500 hover:scale-105">

            <Image
              src={imageUrl}
              alt={anime.title}
              width={300}
              height={400}
              className="w-full h-auto"
              priority
            />

            <div className="absolute inset-0 bg-black/10 group-hover:bg-black/20 transition-all"></div>

          </div>
        </div>

        {/* RIGHT TEXT SECTION (MD: COL 2 & 3) */}
        <div className="md:col-span-2 text-center md:text-left flex flex-col justify-center gap-4">
          <h1 className="text-3xl md:text-5xl font-extrabold text-white drop-shadow-lg leading-tight">
            {anime.title}
          </h1>

          <p className="text-white/80 text-lg drop-shadow-sm font-semibold">{titleEnglish}</p>

          {/* STATS GRID (4x) */}
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mt-4">

            {/* 1. Rating */}
            <div className="flex flex-col items-center md:items-start gap-1 bg-card p-3 rounded-lg border border-border shadow-sm">
              <Star className="text-yellow-400" />
              <span className="text-sm text-muted-foreground">Rating</span>
              <span className="text-lg font-bold text-primary">{score}</span>
            </div>

            {/* 2. Episodes */}
            <div className="flex flex-col items-center md:items-start gap-1 bg-card p-3 rounded-lg border border-border shadow-sm">
              <Film className="text-blue-500" />
              <span className="text-sm text-muted-foreground">Episodes</span>
              <span className="text-lg font-bold text-primary">{episodes}</span>
            </div>

            {/* 3. Source (BARU) */}
            <div className="flex flex-col items-center md:items-start gap-1 bg-card p-3 rounded-lg border border-border shadow-sm">
              <BookOpen className="text-red-500" />
              <span className="text-sm text-muted-foreground">Source</span>
              <span className="text-lg font-bold text-primary">{source}</span>
            </div>

            {/* 4. Year */}
            <div className="flex flex-col items-center md:items-start gap-1 bg-card p-3 rounded-lg border border-border shadow-sm">
              <Calendar className="text-purple-500" />
              <span className="text-sm text-muted-foreground">Year</span>
              <span className="text-lg font-bold text-primary">{year}</span>
            </div>

          </div>

          {/* ACTION BUTTONS (Dua tombol Watch + Aksi) */}
          <div className="flex flex-wrap gap-3 mt-4 justify-center md:justify-start">

            {/* Trailer/Watch Button */}
            {trailerUrl ? (
              <Button
                // Menggunakan onClick untuk membuka trailer di tab baru
                className="gap-2 bg-primary text-white hover:bg-primary/90"
                onClick={() => window.open(trailerUrl, '_blank')}
              >
                <Play size={18} /> Watch Trailer
              </Button>
            ) : (
              // Tampilkan tombol non-fungsional jika tidak ada trailer
              <Button disabled className="gap-2 bg-gray-500/50 text-white cursor-not-allowed">
                <Play size={18} /> No Trailer
              </Button>
            )}

            {/* Favorites - Disesuaikan untuk state aktif agar kontras */}
            <Button
              variant="outline"
              className="gap-2 border-primary text-primary 
                         hover:bg-primary/15 
                         active:bg-primary/25 active:text-primary 
                         transition-colors"
            >
              <Heart size={18} /> Add to Favorites
            </Button>

            {/* Share - Disesuaikan untuk state aktif agar kontras */}
            <Button
              variant="outline"
              className="gap-2 border-primary text-primary 
                         hover:bg-primary/15 
                         active:bg-primary/25 active:text-primary 
                         transition-colors"
            >
              <Share2 size={18} /> Share
            </Button>
          </div>
        </div>
      </div>

      {/* --- BAGIAN KONTEN DI BAWAH GRID --- */}
      <div className="relative max-w-6xl mx-auto mt-8 md:mt-12 space-y-12">

        {/* 1. GENRES */}
        {genres.length > 0 && (
          <div className="px-4 sm:px-6 lg:px-8 flex flex-wrap gap-3">
            {genres.map((genre) => (
              <span
                key={genre.mal_id}
                className="px-3 py-1 bg-card border border-primary text-primary text-sm rounded-full transition-colors hover:bg-primary/10"
              >
                {genre.name}
              </span>
            ))}
          </div>
        )}

        {/* 2. SYNOPSIS */}
        <section className="px-4 sm:px-6 lg:px-8">
          <h2 className="text-2xl font-bold text-foreground mb-4">Synopsis</h2>
          <p className="text-muted-foreground leading-relaxed whitespace-pre-line">{synopsis}</p>
        </section>

        {/* 3. AIRING STATUS & DATES (Dipisahkan dari Stats Grid) */}
        <section className="px-4 sm:px-6 lg:px-8">
          <h2 className="text-2xl font-bold text-foreground mb-4">Airing Details</h2>
          <div className="grid grid-cols-1 sm:grid-cols-3 gap-4 text-sm text-muted-foreground">
            <p className='flex items-center gap-2'><Activity size={16} className='text-green-500'/> <strong>Status:</strong> {status}</p>
            <p className='flex items-center gap-2'><Calendar size={16} className='text-gray-400'/> <strong>Aired From:</strong> {airedFrom}</p>
            <p className='flex items-center gap-2'><Calendar size={16} className='text-gray-400'/> <strong>Aired To:</strong> {airedTo}</p>
          </div>
        </section>

        {/* 4. RELATED CONTENT / ADAPTATIONS (BARU - Seperti MAL) */}
        {relations.length > 0 && <RelatedContent relations={relations} />}

        {/* Tambahkan bagian lain (misalnya: Characters, Staff) di sini */}

      </div>
    </main>
  )
}